<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!--卡片视图区-->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input v-model="queryInfo.query" clearable @clear="getGoodsList" placeholder="请输入内容">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList">
            </el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage">添加商品</el-button>
        </el-col>
      </el-row>
    </el-card>
    <!--table表格区域-->
    <el-table :data="goodsList" border stripe>
      <el-table-column type="index" label="#"></el-table-column>
      <el-table-column label="商品名称" prop="goods_name"></el-table-column>
      <el-table-column label="商品价格（元）" prop="goods_price" width="95px"></el-table-column>
      <el-table-column label="商品重量" prop="goods_weight" width="70px"></el-table-column>
      <el-table-column label="商品创建时间" prop="add_time" width="160px">
        <template slot-scope="scope">
          {{scope.row.add_time|dataFormat}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width="200px">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini">修改</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--分页区域-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total" background>
    </el-pagination>


  </div>
</template>

<script>
  export default {
    data() {
      return {
        // 查询参数对象
        queryInfo: {
          query: '',
          pagenum: 1,
          pagesize: 10
        },
        // 商品列表
        goodsList: [],
        // 总数据参数
        total: 0
      }
    },
    created() {
      this.getGoodsList()
    },
    methods: {
      // 根据分页获取对应的商品列表
      async getGoodsList() {
        const {data: res} = await this.$http.get(`goods`, {params: this.queryInfo})
        if (200 !== res.meta.status) {
          return this.$message.error('获取商品列表失败！')
        } else {
          this.$message.success('获取商品列表成功！')
          this.goodsList = res.data.goods
          this.total = res.data.total
        }
      },
      // 分页变化触发函数
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize
        this.getGoodsList()
      },
      // 页码值发生变化
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage
        this.getGoodsList()
      },
      // 根据Id删除商品
      async removeById(id) {
        const confirmResult = await this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        if ('confirm' !== confirmResult) {
          return this.$message.info('已经取消删除！')
        } else {
          const {data: res} = await this.$http.delete(`goods/${id}`)
          if (200 !== res.meta.status) {
            return this.$message.error('删除失败！')
          } else {
            this.$message.success('删除成功！')
            this.getGoodsList()
          }
        }
      },
      // 跳转到新增页面
      goAddpage() {
        // 跳转到新的组件中
        this.$router.push(`/goods/add`)
      }
    },
    name: "List"
  }
</script>

<style scoped>

</style>
